import {YandexMap} from "../map/map";
import {GridTable} from "../grid/grid";
import React, {useContext} from "react";
import {MapGridContext} from "../../pages/Map";

export function ViewOfContent(){
        const {
        defaultState,
        handleObjectClick,
        mapManager,
        objectManager,
        objects,
        rows,
        setSelectedObject,
        apiRef,
        map
    } = useContext(MapGridContext);


    
    return (<>
            {map && <YandexMap defaultState={defaultState} handleObjectClick={handleObjectClick} mapManager={mapManager} objectManager={objectManager} objects={objects} />}
            {!map && <GridTable rows={rows} setSelectedObject={setSelectedObject} apiRef={apiRef} />}
    </>
)
}
