import React, {useContext} from "react";
import {FilterContext} from "../../pages/Map";

export function Filter(){
    const {
        cat,
        reg,
        setMap,
        map,
        selectedObject,
        setSelected,
        setDefaultState,
        objectManager,
        catForMap,
        regForMap
    } = useContext(FilterContext);

    return (
            <nav role='navigation'>
                <div id="menuToggle">
                    <input type="checkbox" />
                    <span></span>
                    <span></span>
                    <span></span>
                    <ul id="menu">
                        {map && (<>
                        <p style={{fontSize: "22px"}}><strong></strong></p>
                        <div className="region" style={{height: "155px"}} >
                            {cat}
                        </div>
                        <li>
                            <p><strong>  </strong></p>
                            <div className="region">
                                {reg}
                            </div>
                        </li>
                        </>)}
                        {!map && (<>
                            <div style={{height: "calc(746px - 190px)"}}>

                            </div>
                        </>)}
                        <li style={{display: "flex", height: "90px", marginTop: "auto", marginBottom: "auto"}}>
                            <div>
                                <p><strong></strong></p>
                                <div className="close_filter" onClick={() => {
                                    setMap(!map);
                                    if(selectedObject && !map){
                                        setSelected(true)
                                        setDefaultState({
                                            center: selectedObject.coordinates ? selectedObject.coordinates : [61.775181, 81.635519],
                                            zoom:  selectedObject.coordinates ? 12 : 4,
                                        })
                                    } else {
                                        setSelected(false)
                                    }
                                }}>
                                    {map ? '' : ''}
                                </div>
                            </div>
                            <div className="close_filter" style={{marginLeft: "20px"}} onClick={() => {
                                objectManager?.current?.setFilter();
                                // init()
                                for(let g = 0; g < catForMap.length; g++) {
                                    document.getElementById(`dynamic-${catForMap[g]}`).style.color = 'black';
                                }
                                for(let g = 0; g < regForMap.length; g++) {
                                    document.getElementById(`dynamic-${regForMap[g]}`).style.color = 'black';
                                }
                            }}>
                                 
                            </div>
                        </li>
                    </ul>
                </div>
            </nav>
    )
}
