import {Map, ObjectManager, TypeSelector, YMaps} from "@pbe/react-yandex-maps";
import React from "react";

export function YandexMap({defaultState, handleObjectClick, mapManager, objectManager, objects}){
    const myStyle = {
        width: "calc(100vw)",
        height: "calc(100vh - 177px)"
    }

    // mapManager.events.add(['boundschange','datachange','objecttypeschange'], function () {
    //     let objects = Map.geoQuery(objectManager.objects).searchInside(mapManager);
        
        
    //     console.log(objects);
    //     });
     

    return (
        <>
            <YMaps>
                <Map
                    defaultState={defaultState}
                    style={myStyle}
                    instanceRef={mapManager}
                >
                    <TypeSelector options={{float: 'left'}}/>
                    <ObjectManager
                        instanceRef={objectManager}
                        options={{
                            clusterize: true,
                            gridSize: 100
                        }}
                        clusters={{
                            groupByCoordinates: false,
                            clusterDisableClickZoom: true,
                            clusterHideIconOnBalloonOpen: false,
                            geoObjectHideIconOnBalloonOpen: false,
                        }}
                        onClick={handleObjectClick}
                        features={objects}
                        modules={[
                            "objectManager.addon.objectsBalloon",
                            "objectManager.addon.objectsHint",
                        ]}
                    />
                </Map>
            </YMaps>
        </>
    )
}
