import { NavLink } from "react-router-dom";
import "./style.css"
import logo from "../../img/logo/logo.png";
import {logoutUser} from "../../services/auth.service";

const activeLink = "nav-list__link nav-list__link--active";
const normalLink = "nav-list__link";
export function Navbar({isAuth, setIsAuth}) {
    const width = window.innerWidth;
    const enter = isAuth ? '' : '';
    return (
        <nav className="nav">
            <div className="container">
                <div className="nav-row">
                    <NavLink to="/" className="logo" >
                        <img src="http://azhb.ru/images/cms/thumbs/a5b0aeaa3fa7d6e58d75710c18673bd7ec6d5f6d/poster033_300_auto.png" height="107px" alt=" . .."/>
                    </NavLink>

                    <ul className="nav-list">
                        <li className="nav-list__item">
                            <NavLink to="/" className={({isActive}) => {
                                return isActive ? normalLink : normalLink;
                            }}>
                                
                            </NavLink>
                        </li>
                        <li className="nav-list__item">
                            <NavLink to="/contacts" className={({isActive}) => {
                                return isActive ? normalLink : normalLink;
                            }}>
                                
                            </NavLink>
                        </li>
                        {isAuth && <li className="nav-list__item">
                            <NavLink to="/profile" className={({isActive}) => {
                                return isActive ? normalLink : normalLink;
                            }}>
                                 
                            </NavLink>
                        </li>}
                        {!isAuth && <li className="nav-list__item">
                            <NavLink to="/register" className={({isActive}) => {
                                return isActive ? normalLink : normalLink;
                            }}>
                                
                            </NavLink>
                        </li>}
                        <li className="nav-list__item">
                            <NavLink to="/login" className={({isActive}) => {
                                return isActive ? activeLink : normalLink;
                            }} onClick={() => {
                                setIsAuth(false);
                                logoutUser();
                            }}>
                                {enter}
                            </NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}
