import Box from "@mui/material/Box";
import {DataGrid, ruRU} from "@mui/x-data-grid";
import React from "react";

export function GridTable({rows, apiRef, setSelectedObject}){

    const handleRowClick = (params) => {
        setSelectedObject({coordinates: params.row.coordinates,
            id: params.id,
            regionObject: params.row.region,
            balloonContent: params.row.firstName,
            direction: params.row.direction,
            inn: params.row.inn})
    };

    const columns = [
        { field: 'id', headerName: 'ID', width: 40 },
        {
            field: 'firstName',
            headerName: '',
            width: 200,
            editable: true,
        },
        {
            field: 'direction',
            headerName: '',
            width: 120,
            editable: true,
        },
        {
            field: 'region',
            headerName: '',
            type: 'string',
            width: 110,
            editable: true,
        },
        {
            field: 'inn',
            headerName: '',
            sortable: false,
            width: 130,

        },
        {
            field: 'ocved',
            headerName: '',
            sortable: false,
            width: 80,

        },
        {
            field: 'address',
            headerName: '',
            sortable: false,
            width: 250,

        },
        {
            field: 'main',
            headerName: '',
            sortable: false,
            width: 250,

        },
        {
            field: 'phone',
            headerName: '',
            sortable: false,
            width: 190,

        },
        {
            field: 'email',
            headerName: '',
            sortable: false,
            width: 160,

        },
        {
            field: 'coordinates',
            headerName: '',
            sortable: false,
            width: 160,
        },
    ];


    return (
        <div className="scroll-table">
            <Box sx={{ height: "calc(100vh - 177px)", width: '100vw' }}>
                <DataGrid
                    localeText={ruRU.components.MuiDataGrid.defaultProps.localeText}
                    rows={rows}
                    columns={columns}
                    initialState={{
                        pagination: {
                            paginationModel: {
                                pageSize: 10,
                            },
                        },
                    }}
                    apiRef={apiRef}
                    onRowClick={handleRowClick}
                    pageSizeOptions={[10]}
                    checkboxSelection={false}
                />
            </Box>
        </div>
    )
}
