import React, {useContext} from "react";
import {FilterContext} from "../../pages/Map";
import {Comment} from "./components/comment/Comment";

export function ObjectInfo(){

    const {
        selectedObject,
    } = useContext(FilterContext);

    return (
        <>
            <strong>
                <h1 className="h1_title">{selectedObject.balloonContent}</h1>
            </strong>
            <h2 className="h2_title"> <strong>:</strong> {selectedObject.regionObject.split('-')[1]}</h2>
            <h2 className="h2_title"> <strong>:</strong> {selectedObject.direction}</h2>
            <h2 className="h2_title"> <strong> :</strong></h2>
            <h2 className="h2_title">{selectedObject.coordinates[0]}     {selectedObject.coordinates[1]}</h2>
            <h2 className="h2_title"><strong>:</strong> {selectedObject.inn}</h2>
            <div style={{display: "flex", flexDirection: "column", bottom: '0', position: "relative"}}>
                <Comment  selectedObject={selectedObject} />
            </div>
        </>
    )
}
